import React from 'react';
import SkillBadge from "../skillBadge";
import SkillProgressEnum from "../../../domain/enums/SkillProgressEnum";
import getColorByLevel from "../../helpers/skillBadge";

export default function SkillsTab(props) {
    return (
        <>
            {props['skills'].map(function (item, key) {
                return <SkillBadge key={key} title={item.title} level={item.level}/>;
            })}

            <hr/>

            <h3>   </h3>

            <ul>
                <li>
                    <SkillBadge title="" level={SkillProgressEnum.VERY_HIGH}/> - <span
                    style={{color: getColorByLevel(SkillProgressEnum.VERY_HIGH).foregroundColor}}>  ( 90)</span>
                </li>
                <li>
                    <SkillBadge title="" level={SkillProgressEnum.HIGH}/> - <span
                    style={{color: getColorByLevel(SkillProgressEnum.HIGH).foregroundColor}}>  ( 75)</span>
                </li>
                <li>
                    <SkillBadge title="" level={SkillProgressEnum.NORMAL}/> - <span
                    style={{color: getColorByLevel(SkillProgressEnum.NORMAL).foregroundColor}}>  ( 50)</span>
                </li>
                <li>
                    <SkillBadge title="" level={SkillProgressEnum.LOW}/> - <span
                    style={{color: getColorByLevel(SkillProgressEnum.LOW).foregroundColor}}>  ( 25)</span>
                </li>
                <li>
                    <SkillBadge title="" level={SkillProgressEnum.VERY_LOW}/> - <span
                    style={{color: getColorByLevel(SkillProgressEnum.VERY_LOW).foregroundColor}}>  ( 5)</span>
                </li>
            </ul>
        </>
    );
};
